@font-face {
    font-family: IRANSans;
    font-style: normal;
    font-weight: bold;
    src: url('../font/IRANSansWeb.eot');
    src: url('../font/IRANSansWeb.woff2') format('woff2'),
        url('../font/IRANSansWeb.woff') format('woff'),
        url('../font/IRANSansWeb.ttf') format('truetype');
}

html,
body,
div,
input,
a,
h1,
h2,
h3,
h4,
h5,
p,
h6,
span,
table,
tr,
th,
button,
form,
thead,
tbody,
ul,
li,
option,
select,
td,
svg {
    font-family: IRANSans, serif !important;
    direction: rtl !important;
    text-align: right !important;
}

label{ font-family: IRANSans, serif !important;
    direction: rtl !important;
    text-align: right !important;
    align-items: end;

}

.font-persian {
    font-family: IRANSans, serif !important;
    direction: rtl !important;
    text-align: right !important;
    align-items: right;

}

